<template>
  <div id="app" v-show="showPage" :class="[show ? 'back' : '']">
    <router-view/>
  </div>
</template>

<script>
import Bus from './tools/eventBus/index.js';
export default {
    data() {
        return {
            show: false,
            showPage: false,
            poptipWav: require('./assets/audio/4204.wav'),
            poptipMp3: require('./assets/audio/4204.mp3'),
            voiceStatus: true
        }
    },
    mounted() {
        let that = this;
        let voiceStatus = localStorage.getItem('voiceStatus');
        if (voiceStatus)
            that.voiceStatus = voiceStatus == 1 ? true : false;

        Bus.$on('showBack', function() {
            that.showPage = true;
            that.show = true;
        })

        Bus.$on('showPage', function() {
            that.showPage = true;
        })

        Bus.$on('voiceStatus', function() {
            let voiceStatus = localStorage.getItem('voiceStatus');
            if (voiceStatus)
                that.voiceStatus = voiceStatus == 1 ? true : false;
        })

        that.handleListenBranch();
        window.sendMessage = function() {
            that.handleSendMessage();
            setTimeout(function() {
                if (window.sendMessage)
                    window.sendMessage();
            }, 240000)
        }
        window.sendMessage();
    },
    methods: {
        handleSendMessage() {
            let that = this;

            that.$axios({
                url: '/api/Merchant/OnlineSupport'
            })
        },
        handleListenBranch() {
            let that = this;
            that.getApplyData().then(function(data) {
                if (data.data) {
                    if (that.voiceStatus) {
                        let status = data.data.some(item => item.Management == 3);

                        if (status) {
                            let au = document.createElement('audio');
                            au.preload = 'auto';
                            au.src = that.poptipWav;
                            au.src = that.poptipMp3;
                            au.play();
                        }
                    }
                    Bus.$emit('upperBranch', data.data);
                }
                setTimeout(function() {
                    that.handleListenBranch();
                }, 5000)
            }).catch(function(e) {
                setTimeout(function() {
                    that.handleListenBranch();
                }, 5000)
            })
        },
        getApplyData() {
            return new Promise((resolve, reject) => {
                let that = this;

                that.$axios({
                    url: '/api/Merchant/GetUserApply'
                }).then(function(data) {
                    let result = null;
                    if (data.data.Status === 100) {
                        result = data.data.Data.filter(item => item.Management === 3 && item.Status === '正常');
                    }
                    that.isLoadingPoptip = false;
                    resolve({type: true, data: result});
                }).catch(function(e) {
                    resolve({type: false, data: null})
                })
            })
        },
    }
}
</script>

