import Bus from '../eventBus/index.js'

const lotterySignalr = {
    signalr: null,
    flag: 0, // 心跳异常次数
    userInfo: null, // 用户信息
    interval: null, // 心跳检测计时
    status: null, // 关闭标示，null异常关闭，active主动关闭
    lastReceiver: null, // 最后一次收包时间, 单位秒
    nextReceiver: null, // 下一次收包最大间隔时间, 单位秒
    timeout: null, // 重连倒计时,
    isConnect: false, // 是否在链接中
    getLocalStorage() {
        var isAndroid = (/android/gi).test(navigator.appVersion),
          ls = window.localStorage;
        if(isAndroid) {
          try {
            ls = os.localStorage();
          }catch(e) {
            ls = window.localStorage;
          }
        }
        return ls;
    },
    getCookie(key) {
        let c_start = '',
            c_end = '';
        if(document.cookie.length > 0){
            c_start = document.cookie.indexOf(key + "=");
            if(c_start != -1){
                c_start = c_start + key.length + 1;
                c_end = document.cookie.indexOf(";", c_start);
                if(c_end == -1){
                    c_end = document.cookie.length;
                }
                return unescape(document.cookie.substring(c_start, c_end));
            }
        }
        return null;
    },
    setCookie(key, value, expireDays) {
        let that = this;
        var exDate = new Date();
        exDate.setDate(exDate.getDate() + expireDays);
        let domain;
        try {
            domain = location.hostname || that.getHostUrl().split('://')[1];
        } catch(e) {
            domain = that.getHostUrl() || '127.0.0.1';
        }
        document.cookie = key + "=" + escape(value) + ((expireDays==null) ? "" : ";expires=" + exDate.toGMTString()) + "; path=/;domain=" + domain;
    },
    getLocalData(key) {
        let that = this,
            res = that.getLocalStorage().getItem(key);
        if (!res) {
            res = that.getCookie(key);
        }
        return res;
    },
    setLocalData(key, value) {
        let that = this;
        if (value) {
            that.getLocalStorage().setItem(key, value);
            that.setCookie(key, value, 365);
        }
    },
    init() {
        let that = this;
        that.isConnect = true; // 链接开始，标记状态
        if (that.signalr) {
            if (that.signalr.connection.connectionState === 1) {
                that.signalr.off();
                that.signalr.stop();
            }
        }

        that.signalr = new signalR.HubConnectionBuilder().withUrl(`/sign/chatHub`).build();

        //封盘时间
        that.signalr.on("BackstageGameInfo", function(message) {
            that.lastReceiver = parseInt(new Date().getTime() / 1000);
            that.nextReceiver = that.lastReceiver + 7;
            that.flag > 0 && that.flag--;
            Bus.$emit('BackstageGameInfo', message);
        })

        console.log('链接开始建立')
        that.status = null; // 默认为异常关闭
        that.signalr.start()
        .then(function() {
            console.log('链接建立成功');
            that.isConnect = false; // 链接异常，重置链接标识
            that.lastReceiver = parseInt(new Date().getTime() / 1000);
            that.nextReceiver = that.lastReceiver + 7;
            that.signalr.onclose(function() {
                console.log('链接网络关闭');
                clearInterval(that.interval);
                that.interval = null;
                that.flag = 0; // 链接异常，重置异常标识
                that.isConnect = false; // 链接异常，重置链接标识
                if (!that.status) { // 只有异常关闭才重连
                    that.reStart(3000);
                    Bus.$emit('startReConnect');
                }
            });
            that.heartbeatCheck(); // 开启心跳检测
            if (that.flag > 2) { // 重连成功
                Bus.$emit('endReConnect');
            } else {
                Bus.$emit('restart');
            }
            that.flag = 0;
        })
        .catch(function(err) {
            console.log(err, that.signalr);
            console.log('链接异常关闭');
            clearInterval(that.interval);
            that.interval = null;
            that.flag = 0; // 链接异常，重置异常标识
            that.isConnect = false; // 链接异常，重置链接标识
            Bus.$emit('handleExitRoom');
            that.reStart(5000);
        })
    },
    reStart(time) {
        let that = this;

        if (that.signalr) {
            that.signalr.off();
            that.signalr.stop();
            that.signalr.onclose(function() {
                console.log('重新注册关闭事件');
            });
        }

        if (that.timeout && !that.isConnect) {
            clearTimeout(that.timeout);
            that.timeout = null;
        }

        if (that.interval) {
            clearInterval(that.interval);
            that.interval = null;
        }

        if (that.isConnect) {
            console.log('正在重连中，请不要重复链接');
            return;
        }

        that.timeout = setTimeout(function() {
            if (!that.isConnect) {
                console.log('开始重连');
                that.init();
                clearTimeout(that.timeout);
                that.timeout = null;
            } else {
                console.log('链接中，请不要重复链接');
            }
        }, time);
    },
    heartbeatCheck() {
        let that = this;

        if (that.interval) {
            console.log('定时器已存在！请不要重复创建');
            return;
        }
        that.interval = setInterval(function() {
            if (that.lastReceiver && that.nextReceiver) {
                that.lastReceiver++;
                if (that.lastReceiver > that.nextReceiver) {
                    that.lastReceiver = that.nextReceiver;
                    that.nextReceiver = that.lastReceiver + 7;
                    that.flag++;
                    that.heartbeatSend();
                }

                if (that.flag > 2) {
                    clearInterval(that.interval);
                    that.interval = null;
                    that.lastReceiver = null;
                    that.nextReceiver = null;
                    that.reStart(3000);
                    Bus.$emit('startReConnect');
                }
            }
        }, 1000);
    },
    heartbeatSend() {
        let that = this;
        if (that.signalr.connection.connectionState === 1) {
            that.signalr.invoke("Heart", 'OK').catch(function(err) {
            });
        }
    },
    changeBaseSetting() {
        let that = this;
        let vInterval_ = setInterval(function() {
            if (that.signalr && that.signalr.connection.connectionState === 1) {
                let id = localStorage.getItem('merchantID');
                if (id) {
                    that.signalr.invoke("InitBackstage", id).catch(function(err) {
                    });
                }
                clearInterval(vInterval_);
                vInterval_ = null;
            }
        }, 100);
    },
    closeSignalr() {
        let that = this;
        return new Promise(function(resolve, reject) {
            try {
                clearInterval(that.interval);
                that.interval = null;
                that.status = 'active'; // 主动关闭
                if (that.signalr) {
                    that.signalr.off();
                    that.signalr.stop();
                }
                resolve();
            } catch(e) {
                reject();
            }
        })
    }
}

export default lotterySignalr;