import Vue from 'vue'
import Router from 'vue-router'
import Bus from './tools/eventBus/index.js';

const Home = () => import('./views/home/index.vue');
const Login = () => import('./views/login/index.vue');
const Index = () => import('./views/index/index.vue');
const Base = () => import('./views/baseSetting/index.vue');
const UserManagement = () => import('./views/userManagement/index.vue');
const FieldMonitor = () => import('./views/fieldMonitor/index.vue');
const ReportForm = () => import('./views/reportForm/index.vue');
const Backwater = () => import('./views/backwater/index.vue');
const OddsLimit = () => import('./views/oddsLimit/index.vue');
const RoomSetting = () => import('./views/roomSetting/index.vue');
const Agent = () => import('./views/agent/index.vue');
const AgentReport = () => import('./views/agentReport/index.vue');
const Other = () => import('./views/other/index.vue');
const AdminInfo = () => import('./views/adminInfo/index.vue');
const FalseUser = () => import('./views/falseUser/index.vue');
const SensiOperaLog = () => import('./views/sensiOperaLog/index.vue');
// const OuterReport = () => import('./views/outerReport/index.vue');
const GameSetting = () => import('./views/gameSetting/index.vue');
const ManualUpDown = () => import('./views/manualUpDown/index.vue');
const ManualLottery = () => import('./views/manualLottery/index.vue');
const AccountPassword = () => import('./views/accountPassword/index.vue');
const FlyAlone = () => import('./views/flyAlone/index.vue');
const FlyFailRecord = () => import('./views/flyFailRecord/index.vue');
const HtmlDomain = () => import('./views/htmlDomain/htmlDomain.vue');

Vue.use(Router)

const router = new Router({
  mode: 'hash',
  base: process.env.BASE_URL,
  routes: [
    {
      path: '/',
      component: Home,
      redirect: '/login',
      children: [
        { path: 'index', name: 'index', component: Index },
        { path: 'base', name: 'base', component: Base, redirect: '/base/gameSetting', children: [
          { path: 'gameSetting', name: 'gameSetting', component: GameSetting }
        ]},
        { path: 'userManage', name: 'userManage', component: UserManagement },
        { path: 'fieldMonitor', name: 'fieldMonitor', component: FieldMonitor },
        { path: 'reportForm', name: 'reportForm', component: ReportForm },
        { path: 'backwater', name: 'backwater', component: Backwater },
        { path: 'oddsLimit', name: 'oddsLimit', component: OddsLimit },
        { path: 'roomSetting', name: 'roomSetting', component: RoomSetting },
        { path: 'agent', name: 'agent', component: Agent },
        { path: 'agentReport', name: 'agentReport', component: AgentReport },
        { path: 'falseUser', name: 'falseUser', component: FalseUser },
        {
          path: 'other',
          name: 'other',
          component: Other,
          redirect: '/other/adminInfo',
          children: [
            { path: 'adminInfo', name: 'adminInfo', component: AdminInfo },
            { path: 'sensiOperaLog', name: 'sensiOperaLog', component: SensiOperaLog },
            { path: 'manualUpDown', name: 'manualUpDown', component: ManualUpDown },
            { path: 'manualLottery', name: 'manualLottery', component: ManualLottery },
            { path: 'accountPassword', name: 'accountPassword', component: AccountPassword},
            { path: 'flyAlone', name: 'flyAlone', component: FlyAlone },
            { path: 'flyFailRecord', name: 'flyFailRecord', component: FlyFailRecord },
            { path: 'htmlDomain', name: 'htmlDomain', component: HtmlDomain }
          ]
        }
      ]
    },
    {
      path: '/login',
      name: 'login',
      component: Login
    },
    {
      path: '*',
      redirect: '/login'
    },
  ]
})

router.beforeEach((to, from, next) => {
  // ...
  document.title = 'WDBET管理后台';
  if (to.path.indexOf('login') > -1) {
    setTimeout(function() {
      Bus.$emit('showPage');
    }, 100)
  }
  next();
})

export default router