import Vue from 'vue'
import App from './App.vue'
import router from './router'
import './plugins/axios'
import store from './store'
import lotterySignalr from './tools/signalr/index.js'
import {getLocalData, setLocalData, delLocalData} from './tools/storage/index'

import './tools/date/index.js'

import iView from 'iview';
import 'iview/dist/styles/iview.css';

import loading from './component/loading.vue';

import './common/date'

Vue.use(iView)
Vue.component('loading', loading)

Vue.config.productionTip = false
Vue.prototype.getLocalData = getLocalData;
Vue.prototype.setLocalData = setLocalData;
Vue.prototype.delLocalData = delLocalData;
Vue.prototype.lotterySignalr = lotterySignalr;

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')
